import { Component } from '@angular/core';
import { IonicPage, NavController,LoadingController,ModalController, AlertController, NavParams } from 'ionic-angular';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import Format from 'string-format';

@IonicPage()
@Component({
  selector: 'page-chequerequest',
  templateUrl: 'chequerequest.html',
})
export class ChequerequestPage {
  account: string;
  accounts: any = [];
    accounts_no: any=[];
    type: any ;
    langs: any ;
    error: number =0;
  objectblock: any = {
    username: '',account_from:'',lang:'en',startleaf:'',stopleaf:'',type:'1'
    }; 
  cheques: any = [
    { name: 'PERSONAL CHEQUE 50 LEAVES', no: '1' },
    { name: ' COMPANY CHEQUE 50 LEAVES', no: '2' },
    { name: 'CORPORATE - GOVERNMENT CHEQUE 100 LEAVES', no: '3' },
    { name: 'CORPORATE BACK STAMP 100 LEAVES', no: '4' },
    { name: 'BANKER CHEQUE 100 LEAVES', no: '5' }
  ];

   unmasked_accounts: any = [];
    confirmReq: any;
  constructor(public navCtrl: NavController,public modalCtrl: ModalController,private clientdata: ClientdataProvider,public globalVars: GlobalVarsProvider, public navParams: NavParams,
    public loadingController: LoadingController, public alertService: AlertServiceProvider, public alertCtrl: AlertController) {
   this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    this.objectblock.account_from = this.accounts[ 0 ].acc_number;
  }
  goHome() {
    this.navCtrl.setRoot('HomePage');
  } 

  ionViewDidLoad() {
    console.log('ionViewDidLoad ChequerequestPage');
  }

  submit() {
    if ( this.objectblock.account_from === null || this.objectblock.account_from === '' ) {
      this.alertService.errorPop( this.langs.valid_acc );
    } else {
      let message = Format( this.langs.cheque_req_meso, this.objectblock.account_from );
      let template = "<div>" + message + "</div>";
      let obj = { body: "", template: template, ttype: "", completed: false, pageTo: '' };
      let myModal = this.modalCtrl.create( 'ConfirmModalPage', obj );
      myModal.present();
      myModal.onDidDismiss( data => {
        if ( data ) {
          this.request();
        }
        else { }
      } );
    }
  }

  ionViewDidEnter() {
    this.globalVars.reload();

  }  
  request() {
    this.alertService.showLoading( this.langs.submitting_txt );  
    this.clientdata.requestcheque( this.objectblock )
      .subscribe( data => {
        if ( data.length != 0 ) {
          data = this.globalVars.testdec( data );
          console.log( data )
          this.alertService.dismiss();
          if ( !data.error ) {
            this.alertService.errorPop(data.field48);            
          } else if ( data.error ) {
            this.alertService.errorPop(data.field48);   
          } else {
            this.alertService.errorPop( this.langs.tech );
            this.navCtrl.pop();
          }
        } else {
          this.alertService.myLoadingDismiss();
          this.alertService.errorPop( this.langs.tech );
          this.navCtrl.pop();
        }
  }, err => {
        console.log( err );
        this.alertService.dismiss();
        this.alertService.errorPop( this.langs.internet );
      });
  }

  stop() {

    if ( this.objectblock.account_from === null || this.objectblock.account_from === '' ) {
      this.alertService.errorPop( this.langs.valid_acc );
    } else if ( this.objectblock.startleaf === null || this.objectblock.startleaf === '' ) {
      this.error = 1;
    } else if ( this.objectblock.stopleaf === null || this.objectblock.stopleaf === '' ) {
      this.objectblock.stopleaf = this.objectblock.startleaf;
    }
    else {
      let message = Format( this.langs.stop_cheque_meso,  this.globalVars.Username );
      let template = "<div>" + message + "</div>";
      let obj = { body: "", template: template, ttype: "", completed: false, pageTo: '' };
      let myModal = this.modalCtrl.create( 'ConfirmModalPage', obj );
      myModal.present();
      myModal.onDidDismiss( data => {
        if ( data ) {
          this.requeststop();
        }
        else { }
      } );
    }
  }

  requeststop() {
    this.alertService.showLoading( this.langs.submitting_txt );
      this.clientdata.stopcheque( this.objectblock )
        .subscribe( data => {
          data = this.globalVars.testdec( data );
          console.log( data )
          if ( data.length != 0 ) {
            this.alertService.dismiss();
            if ( !data.error ) {
              this.alertService.errorPop(data.field48); 
            } else if ( data.error ) {
              this.alertService.errorPop(data.field48); 
            } else {
              this.alertService.errorPop( this.langs.tech );
            }
          } else {
            this.alertService.dismiss();
            this.alertService.errorPop( this.langs.tech );
            this.navCtrl.pop();
          }
        }, err => {
          console.log( err );
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
        });
    }


}



